.container-main{
    background-color: #22c25f;
    display: flex;
    color: white;
    justify-content: space-between;
    align-items: center;
    height: 65px;
}


.container-main ul{
    list-style: none;
    color: white;
    display: flex;
}


.container-main ul li{
    padding: 0 20px;
    color: white;
}

.container-main ul li a{
    text-decoration: none;
    color: white;
}

.container-main img{
    display: flex;
    height: 60px ;
    width: fit-content;
}