.container-home{
    height: calc(100vh - 130px);
    display: flex;
    align-items: center;
    justify-content: center;
}

.container-home img{
    width: 30%;
}

.container-home div{
    max-width: 500px;
}

.container-home div h1{
    margin: 20px 0;
}

.container-home div p{
    margin: 20px 0;
    max-width: 90%;
    line-height: 1.5;
    font-size: 30px;
    font-family: 'Be Vietnam Pro', sans-serif;
}

.container-home div button{
    background-color: #22c25f;
    color: white;
    width: 150px;
    border: none;
    border-radius: 25px;
    height: 35px;
}